/* blog details page */
.blog-details h2 span,
.deleteIcon {
   word-wrap: break-word;
   color: #f1356d;
}
.blog-details div {
   border-radius: 5px;
   margin: 20px 0;
}
.blog-details h6 {
   word-wrap: break-word;
   font-weight: 900;
   padding-left: 10px;
}
.blog-details p {
   text-align: justify;
   word-wrap: break-word;
   padding: 10px;
   color: #1c1c1c;
}

.blog-details .editIcon {
   cursor: pointer;
   padding-left: 10px;
}

#author {
   color: rgb(202, 202, 202) !important;
}

.clickDiv {
   font-weight: 900;
   font-size: 25px;
}

#blogBody {
   text-align: center;
}

#blogBody .ql-container {
   border: none !important;
   background-color: rgba(245, 245, 245, 0.568);
   padding: 0 !important;
   margin: 0 !important;
   color: #1c1c1c;
}

#blogBody .ql-container {
   border: none !important;
   background-color: rgba(245, 245, 245, 0.568);
   padding: 0 !important;
   margin: 0 !important;
   color: #1c1c1c;
}
#blogBody .ql-editor {
   border: none !important;
   background-color: transparent;
   padding: 10px !important;
   color: #1c1c1c;
}
/*Tool bar styles*/
#blogBody .ql-toolbar {
   border: none !important;
   background-color: #f5f5f5c7 !important;
   padding: 0 !important;
   margin: 0 !important;
   height: 0px;
   visibility: hidden;
}

